.interesno {
    .name {
      background: url("../img/inp-logo.png") no-repeat 98% center;
      background-color: #f4f4f4; }

    .phone {
      background: url("../img/inp-phone.png") no-repeat 98% center;
      background-color: #f4f4f4; }

    .bg-gradient {
      background: linear-gradient(172deg, #98e05f 0%, #0de3ac 100%); }

    .checkbox-round {
      position: absolute;
      top: 0;
      left: 0;
      width: fit-content;
      height: 100%;
      opacity: 0;
      z-index: 1; }


    .mark-check {
      width: 28px;
      height: 28px;
      border: 1px solid #ccc;
      box-sizing: border-box;
      border-radius: 50%;
      position: relative; }

    .checkbox > input:checked + .mark-check::after {
      content: "✓";
      color: green;
      font-size: 20px;
      font-weight: 900;
      position: absolute;
      left: 6px;
      top: -3px;
      opacity: 1; }




